* {
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    font-family: 'Quicksand', sans-serif;
    background-color: #2A1E0E;
    color: #EFD8A1;
}

h1, h2 {
    color: #EFB775;
    font-family: 'Press Start 2P', cursive;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
}

h1 {
    font-size: 42px;
}

h2 {
    padding-top: 10px;
    padding-bottom: 10px;
}

p {
    text-align: left;
}

hr {
    border-top: 1px solid #EFD8A1;
    border-bottom: 0;
}

a, :visited {
    color: #EFB775;
}

button {
    color: #EFD8A1;
    font-size: 100%;
    font-family: 'Press Start 2P', cursive;
    padding: 15px;
    border: transparent;
    background-color: #214640;
    text-decoration: none;
    border-radius: 2px;
}

.blocks {
    text-align: center;
}

.block {
    display: inline-block;
    padding-right: 20px;
    padding-bottom: 50px;
    max-width: 500px;
    text-align: justify;
    vertical-align: top;
}

.inline {
    display: inline-block;
}

.cta {
    text-align: center;
    padding-top: 30px;
}